
import { defineComponent } from "vue";
import WeatherListItem from "./WeatherListItem.vue";
import { ActionTypes } from "@/store/action-types";

export default defineComponent({
    name: "WeatherList",
    components: {
        WeatherListItem,
    },
    data() {
        return {};
    },
    computed: {
        weatherList() {
            return this.$store.getters.getCurrentWeatherList;
        },
    },
    mounted() {
